* Convert PX to EM
*
* em-convert is a function of the gulp postcss-em package
*
**/
/**
* Example
**/
/**
* Media queries
**/
.mini-cards-wrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.mini-cards {
  width: 100%;
  min-height: 194px;
  background-color: var(--clr-grey);
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 0;
  overflow: hidden;
}
.mini-cards::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.231372549);
  z-index: 2;
  transition: all 0.5s ease-in-out;
}
.mini-cards:hover .mini-cards__button {
  color: var(--clr-white);
}
.mini-cards:hover .mini-cards__text-arrow {
  opacity: 1;
  transform: translateX(0);
}
.mini-cards:hover:after {
  transform: translateX(100%) scaleX(0);
}
.mini-cards:hover .mini-cards__title,
.mini-cards:hover .mini-cards__text {
  color: var(--clr-smoke-2);
}
.mini-cards:hover .mini-cards__img img {
  transform: scale(1.1);
}
.mini-cards__wrapper-info {
  text-align: center;
  position: relative;
  z-index: 3;
  padding: 0 0 0 25px;
  text-align: left;
}
@media only screen and (min-width: 992px) {
  .mini-cards__wrapper-info {
    text-align: left;
  }
}
.mini-cards__title {
  margin-bottom: 10px;
  color: var(--clr-white);
}
@media only screen and (min-width: 1440px) {
  .mini-cards__title {
    font-family: var(--font-family-1);
    font-size: var(--theme-font-btn-tab-1);
    color: var(--clr-white);
    line-height: 62px;
    transition: all 0.5s ease-in-out;
  }
}
.mini-cards__text {
  margin-bottom: 10px;
  color: var(--clr-white);
  font-family: var(--font-family-2);
  font-size: var(--theme-font-p-mob-3);
  color: var(--clr-white);
  line-height: 30px;
  transition: all 0.5s ease-in-out;
}
@media only screen and (min-width: 1440px) {
  .mini-cards__text {
    margin-bottom: 28px;
  }
}
.mini-cards__text-arrow {
  opacity: 0;
  transition: all 0.6s ease-in-out;
  transform: translateX(-100%);
}
.mini-cards__button {
  margin: 0 auto 40px;
  font-family: var(--font-family-1);
  font-size: var(--theme-font-btn-tab-1);
  color: var(--clr-white);
  line-height: normal;
  transition: all 0.5s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: none;
  width: fit-content;
  position: relative;
  flex-direction: column;
}
@media only screen and (min-width: 992px) {
  .mini-cards__button {
    margin-left: 0;
    min-width: 216px;
    flex-direction: row;
  }
}
.mini-cards__img {
  position: absolute;
  width: 100%;
  height: 100%;
}
.mini-cards__img img {
  width: 100%;
  min-height: 260px;
  object-position: left;
  height: 100%;
  object-fit: cover;
  max-width: none;
  margin: 0 auto;
  z-index: 2;
  position: relative;
  transition: all 0.5s ease-in-out;
  cursor: pointer;
}
.mini-cards--middle .mini-cards__img img {
  min-height: 0;
  height: 240px;
  width: 100%;
  object-position: bottom;
}
.mini-cards--bottom .mini-cards__img img {
  min-height: 0;
  height: 240px;
  width: 100%;
  object-position: bottom;
}