/**
* Example
**/
/**
* Media queries
**/
.promo-card {
  width: 100%;
  min-height: 622px;
  background-color: var(--clr-grey);
  margin-bottom: 20px;
  position: relative;
  overflow: hidden;
}
@media only screen and (min-width: 992px) {
  .promo-card {
    display: flex;
  }
}
@media only screen and (min-width: 1200px) {
  .promo-card {
    align-items: flex-start;
  }
}
.promo-card:hover .promo-card__button {
  color: var(--clr-white);
}
.promo-card:hover .promo-card__text-arrow {
  opacity: 1;
  transform: translateX(0);
}
.promo-card__wrapper-info {
  text-align: center;
  position: relative;
  z-index: 3;
}
@media only screen and (min-width: 992px) {
  .promo-card__wrapper-info {
    text-align: left;
  }
}
.promo-card__subtitle {
  letter-spacing: 4.3px;
  color: var(--clr-white);
  text-transform: uppercase;
}
@media only screen and (min-width: 1440px) {
  .promo-card__subtitle {
    margin-bottom: 19px;
  }
}
.promo-card__title {
  margin-bottom: 10px;
}
@media only screen and (min-width: 1440px) {
  .promo-card__title {
    font-family: var(--font-family-1);
    font-size: var(--theme-font-h1-mob-4);
    color: var(--clr-white);
    line-height: 62px;
  }
}
.promo-card__text {
  margin-bottom: 10px;
  font-family: var(--font-family-2);
  font-size: var(--theme-font-p-mob-3);
  color: var(--clr-white);
  line-height: 30px;
}
@media only screen and (min-width: 1440px) {
  .promo-card__text {
    margin-bottom: 28px;
  }
}
.promo-card__text-arrow {
  opacity: 0;
  transition: all 0.6s ease-in-out;
  transform: translateX(-100%);
}
.promo-card__button {
  margin: 0 auto 40px;
  font-family: var(--font-family-1);
  font-size: var(--theme-font-btn-tab-1);
  color: var(--clr-smoke-2);
  line-height: normal;
  transition: all 0.3s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: none;
  width: fit-content;
  position: relative;
  flex-direction: column;
}
@media only screen and (min-width: 992px) {
  .promo-card__button {
    margin-left: 0;
    min-width: 216px;
    flex-direction: row;
  }
}
.promo-card__img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
}
.promo-card__img img {
  width: 100%;
  min-height: 622px;
  height: 100%;
  object-fit: cover;
  max-width: none;
  margin: 0 auto;
  z-index: 2;
  position: relative;
  transition: all 0.6s ease-in-out;
  cursor: pointer;
}
@media only screen and (min-width: 1440px) {
  .promo-card--half {
    align-items: flex-start;
    justify-content: space-between;
    padding: 0;
  }
}
.promo-card--half .promo-card__wrapper-info {
  margin-left: 0;
  margin-top: 48px;
  padding: 0 20px;
  text-align: center;
}
@media only screen and (min-width: 1440px) {
  .promo-card--half .promo-card__wrapper-info {
    max-width: 378px;
    margin-top: 147px;
    margin-left: 78px;
    text-align: left;
  }
}
.promo-card--half .promo-card__title {
  color: var(--clr-white);
}
@media only screen and (min-width: 1440px) {
  .promo-card--half .promo-card__title {
    font-family: var(--font-family-1);
    font-size: var(--theme-font-h1-mob-4);
    color: var(--clr-white);
    line-height: 62px;
  }
}
.promo-card--half .button {
  margin: 0 auto;
  min-width: 0;
  gap: 20px;
}
@media only screen and (min-width: 1440px) {
  .promo-card--half .button {
    margin: 0 auto 0 0;
  }
}
@media only screen and (min-width: 1440px) {
  .promo-card--quarter {
    min-height: 622px;
    align-items: flex-start;
    justify-content: space-between;
    padding: 0;
  }
}
.promo-card--quarter .promo-card__wrapper-info {
  text-align: center;
  max-width: none;
  margin-top: 48px;
  width: 100%;
}
@media only screen and (min-width: 1440px) {
  .promo-card--quarter .promo-card__title {
    font-family: var(--font-family-1);
    font-size: var(--theme-font-btn-tab-1);
    color: var(--clr-smoke-2);
    line-height: 32px;
  }
}
.promo-card--quarter .promo-card__subtitle {
  color: var(--clr-red);
}
.promo-card--quarter .promo-card__text {
  color: var(--clr-smoke-2);
}
.promo-card--quarter .promo-card__button {
  margin: 0 auto;
  min-width: 0;
  gap: 20px;
}
.promo-card--quarter:hover .promo-card__button {
  color: var(--clr-red);
}
.promo-card--secondary {
  background-color: var(--clr-smoke-2);
}
.promo-card--secondary:hover .promo-card__button {
  color: var(--clr-yellow);
}
.promo-card--secondary .promo-card__title,
.promo-card--secondary .promo-card__text,
.promo-card--secondary .promo-card__button {
  color: var(--clr-white);
}
.promo-card--secondary .promo-card__subtitle {
  color: var(--clr-yellow);
}
.promo-card--secondary .promo-card__text-arrow svg {
  fill: var(--clr-yellow);
}
.promo-card--secondary .bg-circle circle {
  fill: var(--clr-yellow);
}