/**
* Convert PX to EM
*
* em-convert is a function of the gulp postcss-em package
*
**/
/**
* Example
**/
/**
* Media queries
**/
.product-card {
  width: 100%;
  height: auto;
}
.product-card.disabled {
  opacity: 0.9;
}
.product-card * {
  cursor: pointer;
}
.product-card__details {
  margin-top: 12px;
}
.product-card__category {
  font-family: var(--font-family-2-1);
  font-size: var(--theme-font-h3-mob-4);
  color: var(--clr-red);
  line-height: normal;
  letter-spacing: 4.3px;
  text-transform: uppercase;
}
@media only screen and (min-width: 1440px) {
  .product-card__category {
    margin-bottom: 12px;
  }
}
.product-card__title {
  font-family: var(--font-family-1);
  font-size: var(--theme-font-h2-mob-4);
  color: var(--clr-smoke-2);
  line-height: 30px;
  text-transform: capitalize;
  margin-bottom: 0px;
}
.product-card__price {
  font-family: var(--font-family-2);
  font-size: var(--theme-font-p-mob-1);
  color: var(--clr-smoke-2);
  line-height: 30px;
}
.product-card__actions {
  position: relative;
  overflow: hidden;
  min-height: 280px;
}
@media only screen and (min-width: 1200px) {
  .product-card__actions {
    min-height: 358px;
  }
}
@media only screen and (min-width: 1440px) {
  .product-card__actions {
    min-height: 471px;
  }
}
.product-card__actions--image {
  position: relative;
}
.product-card__actions--image .img-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.product-card__actions--image .img-wrapper--one {
  z-index: 1;
}
.product-card__actions--image .img-wrapper--one:hover img {
  opacity: 0;
}
.product-card__actions--image .img-wrapper--two {
  z-index: 0;
}
.product-card__actions--image img {
  width: 100%;
  height: auto;
  margin: 0 auto;
  display: block;
  transition: all 0.3s ease-in-out;
  object-fit: cover;
  min-height: 300px;
}
@media only screen and (min-width: 1200px) {
  .product-card__actions--image img {
    min-height: 370px;
  }
}
@media only screen and (min-width: 1440px) {
  .product-card__actions--image img {
    min-height: 480px;
  }
}
.product-card__actions--cart {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  max-height: 65px;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity: 1;
  transition: background-color 0.125s ease-in-out, transform 0.3s ease-in-out;
  z-index: 1;
  border: none;
  background-color: var(--clr-smoke-2);
  font-family: var(--font-family-2);
  font-size: var(--theme-font-btn-mob-3);
  color: var(--clr-white);
  line-height: 30px;
  text-transform: uppercase;
  transform: translateY(100%);
}
.product-card__actions--cart:hover, .product-card__actions--cart.adding {
  background-color: var(--clr-red);
}
.product-card__actions--cart.adding .loader-spinner {
  opacity: 1;
}
.product-card__actions-group {
  position: absolute;
  right: 0;
  width: 100%;
  top: 24px;
  height: auto;
  display: flex;
  flex-direction: column;
  max-width: 50px;
  gap: 4px;
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;
  z-index: 4;
}
.product-card__actions-group button {
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  padding: 14px;
}
.product-card__actions-group button:hover svg path {
  fill: var(--clr-red);
}
.product-card__actions-group button svg {
  width: 100%;
  max-width: 24px;
  height: auto;
  fill: var(--clr-smoke-2);
  transition: all 0.3s ease-in-out;
}
.product-card__actions-group button svg path {
  transition: all 0.3s ease-in-out;
}
.product-card__actions .product-card__action--zoom:hover svg path {
  stroke: var(--clr-red);
}
.product-card__actions:hover .product-card__actions--cart {
  transform: translateY(0);
}
.product-card__actions:hover .product-card__actions-group {
  transform: translateX(-25%);
}
.product-card__actions:hover.image-single .product-card__actions--image img {
  transform: scale(1.05);
}
.product-card__actions.active .product-card__actions--cart {
  transform: translateY(0);
}
.product-card__actions.active .product-card__actions-group {
  transform: translateX(-25%);
}
.product-card__actions.active .product-card__actions--image img {
  transform: scale(1.05);
}