/**
* Media queries
**/
a.button {
  display: block;
  border: none;
  text-decoration: none;
  cursor: pointer;
  width: fit-content;
  padding: 20px 38px;
  text-transform: uppercase;
  overflow: hidden;
  position: relative;
  max-width: 240px;
  display: flex;
  justify-content: center;
  font-family: var(--font-family-2-1);
  font-size: var(--theme-font-2);
  color: var(--clr-white);
  line-height: 24px;
}
a.button:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: var(--clr-white);
  transform: translateX(-105%);
  transition: transform 0.4s ease-in-out;
}
a.button:hover:after {
  transform: translateX(0);
}
a.button span {
  transition: all 0.3s;
  position: relative;
  z-index: 1;
}
a.button--primary, a.button--solid {
  border: 2px solid var(--clr-red);
  background-color: var(--clr-red);
}
a.button--primary:hover, a.button--solid:hover {
  color: var(--clr-red);
}
a.button--secondary {
  border: 2px solid var(--clr-yellow);
  color: var(--clr-black);
  background-color: var(--clr-yellow);
}
a.button--secondary:hover {
  color: var(--clr-yellow);
}
a.button--outline span, a.button--link span {
  color: var(--clr-red);
}
a.button--outline {
  background: transparent;
  border: 2px solid var(--clr-red);
}
a.button--outline:after {
  background: var(--clr-red);
}
a.button--outline:hover span {
  color: var(--clr-white);
}
a.button--link {
  background-color: none;
  border: none;
}
a.button--link span {
  text-decoration: underline;
}