/**
* Media queries
**/
.blog-card-section__title-wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 64px;
}
.blog-card-section h2 {
  font-family: var(--font-family-1);
  font-size: var(--theme-font-h2-mob-2-1);
  color: var(--clr-smoke-2);
  line-height: 44px;
  letter-spacing: normal;
  text-transform: capitalize;
  margin-bottom: 0;
}
.blog-card-section h3 {
  font-family: var(--font-family-1);
  font-size: var(--theme-font-btn-tab-1);
  color: var(--clr-smoke-2);
  line-height: 32px;
  letter-spacing: normal;
  margin-bottom: 16px;
}
.blog-card-section p {
  margin-bottom: 16px;
}
.blog-card-section .blog-block {
  position: relative;
}
.blog-card-section .img {
  overflow: hidden;
  height: 394px;
  margin-bottom: 40px;
}
.blog-card-section .img img {
  width: auto;
  height: 436px;
  -o-object-fit: cover;
  object-fit: cover;
}
.blog-card-section .date {
  position: absolute;
  top: 20px;
  right: 20px;
  height: 100px;
  width: 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: var(--clr-white);
}
.blog-card-section .date span.day {
  font-family: var(--font-family-1);
  font-size: var(--theme-font-btn-tab-1);
  color: var(--clr-smoke-2);
  line-height: 32px;
  letter-spacing: normal;
}
.blog-card-section .date span.month {
  font-family: var(--font-family-2);
  font-size: var(--theme-font-p-mob-3);
  color: var(--clr-smoke-2);
  line-height: 32px;
  letter-spacing: normal;
  text-transform: uppercase;
}