/**
* Media queries
**/
.product-slider-section {
  padding: 0;
}
.product-slider-section .product-tabs {
  display: flex;
  justify-content: space-around;
  gap: 20px;
}
@media only screen and (min-width: 992px) {
  .product-slider-section .product-tabs {
    justify-content: flex-start;
  }
}
.product-slider-section .product-tabs__title {
  font-family: var(--font-family-1);
  font-size: var(--theme-font-h2-mob-1);
  color: var(--clr-smoke-2);
  line-height: 44px;
  letter-spacing: normal;
  text-transform: none;
  opacity: 0.5;
  margin: 0;
}
.product-slider-section .product-tabs__title--active {
  color: var(--clr-black);
  opacity: 1;
}
@media only screen and (min-width: 400px) {
  .product-slider-section .product-tabs__title {
    font-size: var(--theme-font-h2-mob-2);
  }
}
@media only screen and (min-width: 992px) {
  .product-slider-section .product-tabs__title {
    font-size: var(--theme-font-h2-mob-2-1);
    line-height: 44px;
  }
}
.product-slider-section .product-tabs__separator {
  margin: 0 10px;
  width: 1px;
  background-color: var(--clr-smoke-2);
  opacity: 0.5;
}
.product-slider-section .product-tabs__separator--vertical {
  height: 40px;
  width: 1px;
}
.product-slider-section .product-tabs__separator--horizontal {
  height: 1px;
  width: 100%;
  margin: 30px 0 72px;
}
.product-slider-section .product-slider .slick-track {
  display: flex;
  gap: 0 20px;
}
.product-slider-section .product-slider-wrapper {
  position: relative;
}
.product-slider-section .product-slider__controller {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 2;
  pointer-events: none;
}
.product-slider-section .product-slider__controller-button--next,
.product-slider-section .product-slider__controller-button--prev {
  width: 66px;
  height: 66px;
  aspect-ratio: 1;
  background-color: #d9d9d9;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  position: absolute;
  top: 40%;
  cursor: pointer;
  pointer-events: auto;
}
@media only screen and (max-width: 1759px) {
  .product-slider-section .product-slider__controller-button--next,
  .product-slider-section .product-slider__controller-button--prev {
    background-color: var(--clr-black);
    width: 55px;
    height: 55px;
  }
}
@media only screen and (max-width: 1199px) {
  .product-slider-section .product-slider__controller-button--next,
  .product-slider-section .product-slider__controller-button--prev {
    top: 30%;
    width: 35px;
    height: 35px;
  }
}
.product-slider-section .product-slider__controller-button--next svg,
.product-slider-section .product-slider__controller-button--prev svg {
  max-width: 25px;
}
@media only screen and (max-width: 1759px) {
  .product-slider-section .product-slider__controller-button--next svg path,
  .product-slider-section .product-slider__controller-button--prev svg path {
    fill: var(--clr-white);
  }
}
@media only screen and (max-width: 1199px) {
  .product-slider-section .product-slider__controller-button--next svg,
  .product-slider-section .product-slider__controller-button--prev svg {
    max-width: 16px;
  }
}
.product-slider-section .product-slider__controller-button--next {
  left: 100%;
  margin-left: 10px;
}
@media only screen and (max-width: 1759px) {
  .product-slider-section .product-slider__controller-button--next {
    left: auto;
    right: 0;
    border-radius: 0;
    margin-right: 15px;
  }
}
.product-slider-section .product-slider__controller-button--prev {
  right: 100%;
  left: auto;
  margin-right: 9px;
}
@media only screen and (max-width: 1759px) {
  .product-slider-section .product-slider__controller-button--prev {
    right: auto;
    left: 0;
    margin-right: 0;
    border-radius: 0;
    margin-left: 30px;
  }
}
.product-slider-section .product-slider__controller-button--next:after,
.product-slider-section .product-slider__controller-button--prev:after {
  display: none;
}
.product-slider-section .product-slider__controller-button--prev svg {
  transform: rotate(180deg);
}
.product-slider-section .container-full .product-slider__controller-button--next,
.product-slider-section .container-full .product-slider__controller-button--prev {
  background-color: var(--clr-black);
  width: 55px;
  height: 55px;
}
.product-slider-section .container-full .product-slider__controller-button--next svg path,
.product-slider-section .container-full .product-slider__controller-button--prev svg path {
  fill: var(--clr-white);
}
.product-slider-section .container-full .product-slider__controller-button--next {
  left: auto;
  right: 0;
  border-radius: 0;
  margin-right: 15px;
}
.product-slider-section .container-full .product-slider__controller-button--prev {
  right: auto;
  left: 0;
  margin-right: 0;
  border-radius: 0;
  margin-left: 30px;
}