/**
* Media queries
**/
.half-banner-card-section {
  margin-top: 150px;
  padding: 0 0 100px;
}
@media only screen and (min-width: 992px) {
  .half-banner-card-section {
    margin-top: 56px;
  }
}
@media only screen and (min-width: 1200px) {
  .half-banner-card-section {
    padding: 0 0 148px;
  }
}