/**
* Media queries
**/
.arrow-button {
  width: 100%;
  max-width: 200px;
  min-height: 52px;
  height: auto;
  display: flex;
  align-items: center;
  position: relative;
}
.arrow-button:hover .arrow-button__text {
  color: var(--clr-red);
}
.arrow-button:hover .arrow-button__arrow {
  transform: translateX(10px);
}
.arrow-button__text {
  font-family: var(--font-family-1);
  font-size: var(--theme-font-btn-tab-1);
  color: var(--clr-smoke-2);
  line-height: 32px;
  margin: 0;
  transition: all 0.6s ease-in-out;
}
.arrow-button__arrow {
  opacity: 1;
  transition: all 0.6s ease-in-out;
  position: absolute;
  right: 0;
  z-index: 1;
}