
  
@font-face {
    font-family: 'Montserrat Black Italic';
    src: url('./MontserratBlackItalic.eot');
    src: url('./MontserratBlackItalic.eot?#iefix') format('embedded-opentype'),
         url('./MontserratBlackItalic.woff2') format('woff2'),
         url('./MontserratBlackItalic.woff') format('woff'),
         url('./MontserratBlackItalic.ttf') format('truetype'),
         url('./MontserratBlackItalic.svg#MontserratBlackItalic') format('svg');
    font-weight: 900;
    font-style: italic;
    font-display: swap;
}


