/**
* Media queries
**/
.banner-card {
  width: 100%;
  height: auto;
  background-color: var(--clr-grey);
  padding: 40px;
  margin-bottom: 20px;
  position: relative;
}
@media only screen and (min-width: 992px) {
  .banner-card {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
@media only screen and (min-width: 1200px) {
  .banner-card {
    min-height: 358px;
    align-items: flex-start;
  }
}
.banner-card:hover .banner-card__img img {
  transform: scale(1.05);
}
.banner-card:hover .banner-card__button {
  color: var(--clr-red);
}
.banner-card:hover .banner-card__text-arrow {
  opacity: 1;
  transform: translateX(0);
}
.banner-card__wrapper-info {
  text-align: center;
}
@media only screen and (min-width: 992px) {
  .banner-card__wrapper-info {
    text-align: left;
  }
}
@media only screen and (min-width: 1440px) {
  .banner-card__wrapper-info {
    max-width: 283px;
    margin-top: 5px;
  }
}
.banner-card__subtitle {
  letter-spacing: 4.3px;
  text-transform: uppercase;
}
@media only screen and (min-width: 1440px) {
  .banner-card__subtitle {
    margin-bottom: 19px;
  }
}
.banner-card__title {
  margin-bottom: 10px;
}
@media only screen and (min-width: 1440px) {
  .banner-card__title {
    font-family: var(--font-family-1);
    font-size: var(--theme-font-h2-mob-2-1);
    color: var(--clr-smoke-2);
    line-height: 44px;
  }
}
.banner-card__text {
  margin-bottom: 10px;
  font-family: var(--font-family-2);
  font-size: var(--theme-font-p-mob-3);
  color: var(--clr-smoke-2);
  line-height: 30px;
}
@media only screen and (min-width: 1440px) {
  .banner-card__text {
    margin-bottom: 51px;
  }
}
.banner-card__text-arrow {
  opacity: 0;
  transition: all 0.6s ease-in-out;
  transform: translateX(-100%);
}
.banner-card__button {
  margin: 0 auto 40px;
  font-family: var(--font-family-1);
  font-size: var(--theme-font-btn-tab-1);
  color: var(--clr-smoke-2);
  line-height: normal;
  transition: all 0.3s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: none;
  width: fit-content;
  position: relative;
  flex-direction: column;
}
@media only screen and (min-width: 992px) {
  .banner-card__button {
    margin-left: 0;
    min-width: 216px;
    flex-direction: row;
  }
}
.banner-card__img {
  position: relative;
}
.banner-card__img img {
  width: 100%;
  height: auto;
  max-width: 448px;
  margin: 0 auto;
  z-index: 2;
  position: relative;
  transition: all 0.6s ease-in-out;
  cursor: pointer;
}
.banner-card__img:hover img {
  transform: scale(1.05);
}
.banner-card__img .bg-circle {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.banner-card__img .bg-circle svg {
  width: 100%;
  height: 100%;
}
@media only screen and (min-width: 1440px) {
  .banner-card--half {
    min-height: 430px;
    align-items: flex-start;
    justify-content: space-between;
    padding: 69px 23px 33px 57px;
  }
}
.banner-card--secondary {
  background-color: var(--clr-smoke-2);
}
.banner-card--secondary:hover .banner-card__button {
  color: var(--clr-yellow);
}
.banner-card--secondary .banner-card__title,
.banner-card--secondary .banner-card__text,
.banner-card--secondary .banner-card__button {
  color: var(--clr-white);
}
.banner-card--secondary .banner-card__subtitle {
  color: var(--clr-yellow);
}
.banner-card--secondary .banner-card__text-arrow svg {
  fill: var(--clr-yellow);
}
.banner-card--secondary .bg-circle circle {
  fill: var(--clr-yellow);
}