/**
* Convert PX to EM
*
* em-convert is a function of the gulp postcss-em package
*
**/
/**
* Example
**/
/**
* Media queries
**/
@media screen and (min-width: 768px) {
  .container-xxl, .container-xl, .container-lg, .container-md, .container-sm, .container {
    max-width: 1024;
  }
}

@media screen and (min-width: 1400px) {
  .container-xxl, .container-xl, .container-lg, .container-md, .container-sm, .container {
    max-width: 1585.87px;
  }
}